import React, {Component} from 'react';
import {BrowserRouter, Route} from 'react-router-dom'
import './App.css'

import Navbar from './Component/Navbar'
import User from './Pages/User/User'
import About from './Pages/about'
import Home from './Pages/home'
import Playlists from './Pages/playlist'

export const authEndpoint = 'https://accounts.spotify.com/authorize';
// Replace with your app's client ID, redirect URI and desired scopes
const clientId = "9110bb9fbfc4422c85e722040cf63bc8";
const redirectUri = "http://localhost:3000/callback";
const scopes = [
  "user-read-currently-playing",
  "user-read-playback-state"
];

const hash = window.location.hash
  .substring(1)
  .split("&")
  .reduce(function(initial, item) {
    if (item) {
      var parts = item.split("=");
      initial[parts[0]] = decodeURIComponent(parts[1]);
    }
    return initial;
  }, {});
window.location.hash = "";

class App extends Component {
  constructor(){
    super();
    this.state = {
      token: null,
      state: null
    }
  }

  componentDidMount() {
    // Set token
    let _token = hash.access_token;
    if (_token) {
      // Set token
      this.setState({
        token: _token
      });
    }
  }

  state = {
    token: null
  }

  render(){
    return (
        <BrowserRouter>
          <div className="App">
            <header className="App-header">
            </header>

              <Navbar/>
                <Route className ="App-page" component={Home} path="/home">Home</Route>
                <Route className ="App-page" component={Playlists} path="/playlists">Playlists</Route>
                <Route className ="App-page" component={About} path="/about">About</Route>
                <Route className ="App-page" component={User} path="/user">User</Route>
              

              {!this.state.token && (
                <button className = "btn" color = "primary">
                <a
                  
                  href={`${authEndpoint}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scopes.join("%20")}&response_type=token&show_dialog=true`}>
                  Login to Spotify 
                </a>
                </button>
              )}
              {this.state.token && (
                  //pass token to other pages and ajax the data from the page for specific things (i.e. user data)
                <div>
                  <User token={this.state.token}></User> 
                </div>
              )}


          </div>
        </BrowserRouter>
    );
  }
}

export default App;