import React from 'react';
import {NavLink} from 'react-router-dom';

import logo from '../logo.svg'

const Navbar = () => {

    return(

        
            <nav className="nav-wrapper"  style={{ background: '#1DB954' }}>
            <img src={logo} className="App-logo left" alt="logo"></img>
                <div className="container">
                    
                    <ul className="right">
                        <li><NavLink className="nav-tabs" to="/home">Home</NavLink></li>
                        <li><NavLink className="nav-tabs" to="/playlists">Playlists</NavLink></li>
                        <li><NavLink className="nav-tabs" to="/about">About</NavLink></li>
                        <li><NavLink className="nav-tabs" to="/user">Profile</NavLink></li>
                    </ul>

                </div>
            </nav>

    )
}
export default Navbar;